<template>
  <div>
    <van-search v-model="value" placeholder="请输入搜索关键词" show-action :autofocus=true>
      <template #action>
        <div @click="search">搜索</div>
      </template>
    </van-search>
  </div>
</template>

<script>
import {Toast} from "vant";

export default {
  name: "articledetail",
  data() {
    return{
      user:{
        name:'',
        id:0,
        username:'',
        surplus:0
      },
      value: '',
    }
  },
  created() {

  },
  mounted() {

  },
  methods: {
    click_left(){
      this.$router.back()
    },
    search(){
      // console.log(this.value)
      if (this.value.length>0){
        this.$store.commit('setsearch_content',this.value)
        this.$router.push('/searchreturn')
      }else {
       Toast.fail("检查输入")
      }
    }
  }
}
</script>

<style scoped lang="less">
.top{
  border: 1px solid black;
  margin-top: 1px;
}
.top{
  /deep/ .van-cell{
    background-color: #F0FFFF;
  }
}
</style>
