//引入
import Vue from 'vue'  //该行代码可以通过Vue.use()自动导入
import Vuex from 'vuex'

//Vuex是插件，插件都需要use
Vue.use(Vuex)

//通过new Vuex.Store可以创建一个仓库
let store = new Vuex.Store({
    state: {
        user:{
            id:-1,
            username:'',
            surplus:-1,
            name:'',
            headico:''
        },
        src:'1.jpg',
        selected_articleid: -1,
        article:[],
        this_user_article:[],
        this_search_content:"",
        this_login_code:"",
        this_now_index:0,
        this_selected_goods_list:[],
        this_buought_goods_list:[],
        this_buought_goods_id:-1
    },
    mutations: {
        inituser(state,userr){
         state.user = userr
        },
        setheadimg(state,src){
            state.src = src
        },
        set_articleid(state,idd){
            state.selected_articleid = idd
        },
        set_artcle(state,art){
            state.article = art
        },
        set_user_artcle(state,art){
            state.this_user_article = art
        },
        setsearch_content(state,search_content){
            state.this_search_content= search_content
        },
        setlogin_code(state,code){
            state.this_login_code= code
        },
        set_now_index(state,index){
            state.this_now_index= index
        },
        set_selected_goods_list(state,index){
            state.this_selected_goods_list= index
        },
        set_buought_goods_list(state,index){
           state.this_buought_goods_list = index
        },
        set_buought_goods_id(state,index){
            state.this_buought_goods_id = index
        }
    },
    actions: {},
    getters: {}
})

//最后需要将仓库导出去，导出去之后就可以在入口文件main.js中使用仓库了
export default store;
