<template>
<div>
     <div>
         <van-search
             v-model="value"
             show-action
             placeholder="请输入搜索关键词"
             @search="onSearch"
             style="position: fixed;width: 100%"
         >
           <template #action>
             <div @click="onSearch">搜索</div>
           </template>
         </van-search>
     </div>

     <div style="height: 54px"></div>

     <div >

        <div style="width: 30%;position: fixed">
           <span v-for="(i,index) in left_daohang" :key="index" @click="click_detail(index)" style="" >

              <div class="mybtn" :class="{ mybtn_select: isActive === index }" >
                {{i}}
              </div>

           </span>
        </div>

        <div style="width: 65%;float: right;margin-bottom: 125px;margin-right: 3px" v-if="isActive !== 0">
           <div v-for="(i,index) in right" :key="index">

              <div style="border: solid 1px #E6E6E6;border-radius: 10px;margin-bottom: 5px;padding: 10px;padding-bottom: 0" @click="click_good_detail(index)">

                <img :src=i.goodsrc alt="" style="width: 176px;height: 176px">
                <div style="font-size: 15px;color: #434343;height: 20px; overflow: hidden;white-space: nowrap;text-overflow: ellipsis;margin-top: 10px">{{i.goodname}}</div>


                <div style="color:#ff2078;font-size: 12px;margin-top: 10px;height: 20px">
                  <span style="background-color:#ffeff5;border-radius: 5px">&nbsp;&nbsp;{{i.goodgrade}}分&nbsp;&nbsp;</span>
                  <span style="background-color:#ffeff5;border-radius: 5px;margin-left: 5px;" v-if="i.goodlb2!=''&&i.goodlb1.length<5&&i.goodlb2.length<5">&nbsp;&nbsp;{{i.goodlb2}}&nbsp;&nbsp;</span>
                  <span style="background-color:#ffeff5;border-radius: 5px;margin-left: 5px;">&nbsp;&nbsp;{{i.goodlb1}}&nbsp;&nbsp;</span>
                </div>

                <div style="margin-top: 0px;height: 40px;line-height: 40px">
                  <span style="color: #ff416b;font-size: 20px;width: 50%;float: left">¥ {{i.goodprice}}</span>
                  <span style="float:right;color: #999999;font-size: 12px;padding-top: 3px">{{i.goodliulian}} 人买过</span>
                </div>

              </div>
           </div>
           <van-skeleton title :row="20" v-if="gujia"/>
        </div>

        <div v-else style="width: 65%;float: right;margin-bottom: 125px;margin-right: 3px">

          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="orange" height="200px" width="100%" v-if="!gujia">
            <van-swipe-item><img src="https://m15.360buyimg.com/mobilecms/jfs/t1/27911/22/20464/113948/63cb3433F40f535a0/392b3fa52a6a0e06.jpg!cr_1125x449_0_166!q70.jpg" alt="" width="100%"></van-swipe-item>
            <van-swipe-item><img src="https://m15.360buyimg.com/mobilecms/jfs/t1/130514/35/29397/64916/63d70e20Fce0683f8/22d523f784f0f83c.jpg!cr_1125x449_0_166!q70.jpg" alt="" width="100%"></van-swipe-item>
            <van-swipe-item><img src="https://imgcps.jd.com/ling4/10042489330542/5LyR6Zey5pe25YWJ/5paH5Lu25pS257qz5aW95biu5omL/p-61b4e0db000915522e1192c4/17b69d47/cr_1053x420_4_0/s/q70.jpg" alt="" width="100%"></van-swipe-item>
            <van-swipe-item><img src="https://m15.360buyimg.com/mobilecms/jfs/t1/161137/4/31613/120485/6359eae8E9e4d0283/a3d57a3a57fc1e99.jpg!cr_1125x449_0_166!q70.jpg" alt="" width="100%" height="100%"></van-swipe-item>
          </van-swipe>

          <div style="background-color:#f6f6f6;height: 120px;margin-top: 20px;border-radius: 15px;padding: 5px 10px;width: 100%;box-sizing: border-box;margin-bottom: 20px" v-if="!gujia">

            <div style="font-size: 13px;color:#666;margin-bottom: 10px">
              <span>至臻秒杀</span>
              <van-count-down :time="time" style="float: right;">
                <template #default="timeData">
                  <span class="block">{{ timeData.hours }}</span>
                  <span class="colon">:</span>
                  <span class="block">{{ timeData.minutes }}</span>
                  <span class="colon">:</span>
                  <span class="block">{{ timeData.seconds }}</span>
                </template>
              </van-count-down>
            </div>

            <div style="justify-content:space-around;display: flex; width: 100%;margin-top: 15px;color: red">
              <span style="flex:1; width: 25%">
                <img src="https://i4-prosmall-fd.zol-img.com.cn/t_s80x60/g7/M00/0D/01/ChMkLGPFCS6IbGLdAAAO4NvXH1AAALzYgODxmgAAA74834.jpg" alt="" width="100%">
                <div style="text-align: center;font-size: 13px">¥999</div>
              </span>
              <span style="flex:1; width: 25%">
                <img src="https://i1-prosmall-fd.zol-img.com.cn/t_s80x60/g7/M00/0D/01/ChMkK2PFDgGIfLEuAAATGo1eIb0AALzgwKwmAEAABMy907.jpg" alt="" width="100%">
                <div style="text-align: center;font-size: 13px">¥99</div>
              </span>
              <span style="flex:1; width: 25%">
                <img src="https://i2-prosmall-fd.zol-img.com.cn/t_s80x60/g7/M00/0B/05/ChMkLGPBBaaIeTobAAASIhrId9AAALsmQHdfWIAABI6302.jpg" alt="" width="100%">
                <div style="text-align: center;font-size: 13px">¥9.9</div>
              </span>
            </div>

          </div>
          <div v-for="(i,index) in right" :key="index">

            <div style="border: solid 1px #E6E6E6;border-radius: 10px;margin-bottom: 5px;padding: 10px;padding-bottom: 0" @click="click_good_detail(index)">

              <img :src=i.goodsrc alt="" style="width: 176px;height: 176px">
              <div style="font-size: 15px;color: #434343;height: 20px; overflow: hidden;white-space: nowrap;text-overflow: ellipsis;margin-top: 10px">{{i.goodname}}</div>


              <div style="color:#ff2078;font-size: 12px;margin-top: 10px;height: 20px">
                <span style="background-color:#ffeff5;border-radius: 5px">&nbsp;&nbsp;{{i.goodgrade}}分&nbsp;&nbsp;</span>
                <span style="background-color:#ffeff5;border-radius: 5px;margin-left: 5px;" v-if="i.goodlb2!=''&&i.goodlb1.length<5">&nbsp;&nbsp;{{i.goodlb2}}&nbsp;&nbsp;</span>
                <span style="background-color:#ffeff5;border-radius: 5px;margin-left: 5px;" >&nbsp;&nbsp;{{i.goodlb1}}&nbsp;&nbsp;</span>
              </div>

              <div style="margin-top: 0px;height: 40px;line-height: 40px">
                <span style="color: #ff416b;font-size: 20px;width: 50%;float: left">¥ {{i.goodprice}}</span>
                <span style="float:right;color: #999999;font-size: 12px;padding-top: 3px">{{i.goodliulian}} 人买过</span>
              </div>

            </div>
          </div>
          <van-skeleton title :row="20" v-if="gujia"/>
        </div>

      </div>


  <div style="height: 60px;position: fixed;bottom: 60px; width:90%;margin-left: 5%; background-color:#EEEEEE;border-radius: 15px;opacity: 0.9;padding: 0 5px;box-sizing: border-box" v-if="all_price>0">
    <van-button type="danger" round style="padding: 0 20px;float: right;height:46px;margin-top: 7px" @click="zhifu">结算({{selected_goods.length}})</van-button>
    <div style="float: right;color: red;margin-right: 10px;margin-top: 20px">
      <span style="font-size: 14px">合计：</span>
      <span style="font-size: 20px">¥ {{all_price}}</span>
    </div>



  </div>

  <div style="height: 50px"></div>


  <Tab></Tab>
</div>
</template>

<script>

import {Toast} from "vant";
import Tab from "@/components/peihe/Tab";

Toast.setDefaultOptions({ duration: 500 });
export default {
  name: "shopclass",
  components: {Tab},
  data() {
    return {
      value: '',
      left_daohang:["热门推荐","酒水饮料","休闲零食","新鲜水果","手机数码","电脑办公"],
      right:[],
      isActive:0,
      loading: false,
      finished: true,
      selected_goods:[],
      all_price:0,
      li:{},
      gujia:true,
      time: 30 * 60 * 60 * 1000,
    };
  },
  created() {
    this.init_user()
  },
  mounted(){
    // this.check_user()
    this.init_selectedgood()
    this.click_detail(0)
  },

  methods: {
    init_user() {
      this.user = this.$store.state.user
      // console.log(this.user)
    },
    init_selectedgood(){
      this.selected_goods = this.$store.state.this_selected_goods_list
      for (let i = 0; i < this.selected_goods.length; i++) {
        this.all_price += this.selected_goods[i].goodprice
      }
      this.all_price = Math.floor(this.all_price*100)/100
    },
    onSearch(val) {
      Toast(val);
    },
    click_detail(index){
      this.gujia = true
      this.isActive = index
      // this.right = ["热门推荐","手机数码","电脑办公","酒水饮料","休闲零食","新鲜水果","热门推荐","手机数码","电脑办公","酒水饮料","休闲零食","新鲜水果"]
      this.right = []
      Toast.loading("加载中")

      this.$http.get('/goods/class?classname='+this.left_daohang[index]).then(res=>{
        this.right = []
        this.right  = res.data.data
        Toast.clear()
        Toast.success("加载成功！")
        this.gujia = false
      }).catch((res) => {
        Toast.clear()
        Toast.fail('服务器异常获取失败！');
        this.gujia = false
      })

    },
    click_good_detail(index){
      this.li = this.right[index]
      this.li['count'] = 1
      this.selected_goods.push(this.li)
      this.all_price = this.all_price + this.right[index].goodprice
      this.all_price = Math.floor(this.all_price*100)/100
    },
    zhifu(){
      this.check_user()
      this.$router.push('/shopjiesuan')
    },
    sleep(NumMillis) {
      var nowTime = new Date();
      var exitTime = nowTime .getTime() + NumMillis;
      while (true) {
        var now = new Date();
        if (now.getTime() > exitTime)
          return;
      }
    },

    check_user(){

      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        return false
      }
      return true
    },
  },
}
</script>

<style scoped lang="less">
//.mybtn:hover{
//  color: red;
//}
.mybtn{
  color: #333333;font-size: 14px;padding: 1px 1px;height: 45px;text-align: center;line-height: 45px;
  background-color:#f8f8f8;
}
.mybtn_select{
  color: #e93b3d;font-size: 14px;padding: 1px 1px;height: 45px;text-align: center;line-height: 45px;
  border-bottom: 1px solid #e93b3d;background-color:#fff;
}
//.my-swipe .van-swipe-item {
//  color: #fff;
//  font-size: 20px;
//  line-height: 150px;
//  text-align: center;
//  background-color: #39a9ed;
//}
.colon {
  display: inline-block;
  margin: 0 4px;
  color: #ee0a24;
}
.block {
  display: inline-block;
  width: 22px;
  color: #fff;
  font-size: 12px;
  text-align: center;
  background-color: #fd4736;
}
</style>
