<template>
<div>

  <van-button type="primary">{{user.username}}{{user.id}}</van-button>

  <div>
    <van-field name="radio" label="哪方获胜">
    <template #input>
      <van-radio-group v-model="radio" direction="horizontal">
        <van-radio name="1">左边</van-radio>
        <van-radio name="2">右边</van-radio>
      </van-radio-group>
    </template>
  </van-field>
    <van-field name="stepper" label="赛事id">
      <template #input>
        <van-stepper v-model="stepper"/>
      </template>
    </van-field>
    <van-button round block type="info" @click="submit">提交</van-button>
  </div>



  <Tab></Tab>

</div>
</template>

<script>
import Tab from './peihe/Tab'
import {Toast} from "vant";
export default {
  name: "Video",
  components: {
    Tab
  },
  data() {
    return{
      user:{
        name:'',
        id:0,
        username:'',
        surplus:0
      },
      radio:'1',
      stepper:1
    }
  },
  created() {
    this.init_user()
    this.check_user_isroot()
  },
  mounted() {
   this.check_user()
  },
  methods:{
    init_user() {
      this.user = this.$store.state.user
    },
    submit(){
      var position = this.radio === '1'?'l':'r'
      this.$http.post("/mesdatas/guess/submit/"+position+"/"+this.stepper).then(res =>{
        Toast.success('结算成功！');
        this.$http.get("/mesdatas/user").then(res =>{
          this.user = res.data.data
          this.$store.commit('inituser',this.user)
        })
      }).catch(res => {
        Toast.fail('服务器异常结算失败！');
      })
    },
    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        this.$router.push('/login')
        return false
      }
      return true
    },
    check_user_isroot(){
      if(!this.user.isroot){
        Toast.fail("无权限！")
        this.$router.back()
        return false
      }
      return true
    },
    }
}
</script>

<style scoped>

