<script>
import Tab from './peihe/Tab'
import {Toast} from "vant";
import {ImagePreview} from "vant";


export default {
  name: "User",
  components: {
    Tab
  },
  data() {
    return{
      loading: false,
      finished: false,
      active:0,
      activeId:"1",
      activeIndex:"0",
      show:false,
      user:{},
      fileList: [],
      fileListreal:[],
      items:[
        {
          text: '浙江',
          children: [
            { text: '杭州', id: "1" },
            { text: '温州', id: "2" },
          ],
        },
        {
          text: '江苏',
          children: [
            { text: '南京', id: "5" },
            { text: '无锡', id: "6" },
          ],
        },
      ],
      imgsrc:{
        fm1:"",
        fm2:"",
        fm3:""
      },
      imgarr:[
        "/headico/user-1.jpg",
        "/headico/user-2.jpg",
        "/headico/user-3.jpg",
        "/headico/user-4.jpg",
        "/headico/user-5.jpg",
        "/headico/user-6.jpg",
        "/headico/user-7.jpg",
        "/headico/user-8.jpg",
        "/headico/user-9.jpg",
        "/headico/user-10.jpg",
        "/headico/user-11.jpg",
        "/headico/user-12.jpg",
      ],
      userimgarr:[

      ],
      allimgarr:[],
    }
  },
  created() {
  this.init_user()
  },
  mounted() {
    this.check_user()
    this.init_img()
  },
  methods:{
    tuichudenglu(){
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    kick(){
      console.log(1)
    },
    init_user() {
      this.user = this.$store.state.user
      this.$store.commit('set_user_artcle',[])
    },
    changehead(){
      // var src = Math.round(Math.random()*9+1)+".jpg"
      // this.user[0].headico= "/headico/user-" + src

      // this.user[0].headico= "http://localhost/headico/user-" + src
      // if (this.$store.state.src === src){
      //   src = Math.round(Math.random()*9+1)+".jpg"
      //   this.userheadico= "http://localhost/headico/user-" + src
      //   this.$store.commit('setheadimg',src)
      //   console.log('重复了！----'+this.$store.state.src)
      // }else {
      //   this.userheadico= "http://localhost/headico/user-" + src
      //   this.$store.commit('setheadimg',src)
      //   console.log(this.$store.state.src)
      // }
      this.show = true
    },
    change_user_head(index){
      // console.log(this.imgarr[index])
      this.imgsrc.fm1 = this.imgarr[index]
      this.$http.post("/articles/users/headico/"+this.user.id,this.imgsrc).then(res=>{
        this.show = false
        // console.log(this.imgarr[index])
        this.user.headico = this.imgarr[index]
        Toast.fail('更改头像成功！');
      }).catch((res) => {
        Toast.fail('服务器异常提交失败！');
      })
    },
    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        this.$router.push('/login')
        return false
      }
      return true
    },
    toarticleuser(){
      this.$router.push('/articleuser')
    },
    chakanlishidingdan(){
      this.$router.push('shophistory')
      // Toast.success('哈哈')
    },
    afterRead(file) {
      this.fileListreal.push(file);
    },
    uploadimg(){
      if (this.fileList.length < 1 ){
        Toast.fail('未选择图片！');
        return
      }

      const formData = new FormData();
      for (let i = 0; i < this.fileListreal.length; i++) {
        formData.append("file",this.fileListreal[i].file)
      }

      this.$http.post("/articles/img/"+this.user.id,formData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          }
      ).then(res=>{
        this.allimgarr = []
        this.userimgarr = []
        this.init_img()

        this.loading = true
        this.finished = false
        this.onLoad()

        this.fileList = []
        this.fileListreal = []
        Toast.fail('上传成功！');
      }).catch((res) => {
        Toast.fail('服务器异常提交失败！');
      })

    },
    init_img(){
     this.$http.get("/articles/img/"+this.user.id).then(res=>{
       for (let i = 0; i < res.data.data.length; i++) {
         this.allimgarr.push(res.data.data[i].name)
       }
       // Toast.fail('加载成功！');
     }).catch((res) => {
       Toast.fail('服务器异常图片加载失败！');
     })
    },
    click_img_detail(index){
      ImagePreview({
        images: this.userimgarr,
        startPosition: index ,
      });
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        var flag = this.userimgarr.length
        for (let i = flag; i < flag+10; i++) {
          // this.userimgarr.push("http://localhost"+this.allimgarr[i]);
          this.userimgarr.push(this.allimgarr[i]);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.userimgarr.length >= this.allimgarr.length) {
          this.finished = true;
        }
      }, 1000);
    },

  }
}
</script>

