<template>
<div style="background-color:#f2f2f2;min-width: 320px;min-height: 800px">

  <van-nav-bar
    title="历史订单"
    left-text="返回"
    left-arrow
    @click-left="onClickLeft"
    :fixed="true"
/>

  <div style="height: 60px;"></div>

  <div style="background-color:#fff;min-height: 800px" v-if="gujia">
    <van-skeleton title avatar :row="5" />
    <van-skeleton title avatar :row="5" />
    <van-skeleton title avatar :row="5" />
    <van-skeleton title avatar :row="5" />
  </div>

  <div style="" v-else>
    <div style="padding: 0 5px">
      <div v-for="(i,index) in historydd" :key="index" style="margin-bottom: 10px;">

       <div style="background-color:white;border-radius: 20px">
        <div style="height: 40px;padding: 10px 10px;">
          <div>
            <span style="float: left;font-size: 14px">订单号：<span style="font-size: 12px">{{i.dingdanid}}</span></span>
            <span style="float: right;font-size: 14px">共{{i.ikun.length}}件 总价格: ¥ <span style="font-weight: bold;font-size: 15px">{{i.allcount}}</span></span>
          </div>
          <div style="font-size: 14px;float: left;margin-top: 5px">下单时间:{{i.time}}</div>
        </div>

        <div v-for="(ii,indexx) in i.ikun" :key="indexx" style="padding-bottom: 2px">

            <div style="padding: 5px 10px;height: 100px;width: 90%;background-color:white;margin: 6px 10px;border-bottom: 1px solid #DDDDDD" v-if="indexx<=2">
              <div style="float: left;width: 20%"><img :src="ii.goodsrc" alt="" width="100%" style="min-width: 20%;max-width: 100px"></div>

              <div style="width: 75%;float: right;padding-left: 5px">
                <div style="float: left;width: 70%">
                  <div style="font-size: 14px;">{{ii.goodname}}</div>
                  <div style="background-color:#2CB12C;width: 80px;font-size: 14px;color: white;text-align: center;border-radius: 5px;margin-top: 5px" v-if="ii.goodlb1.length<5">{{ii.goodlb1}}</div>
                </div>
                <div style="float: right;width: 28%;padding-right: 2%">
                  <div style="font-size: 12px;">¥<span style="font-size:18px ">{{ii.goodprice}}</span></div>
                  <div style="color:#999999;font-size: 14px;">×1</div>
                </div>
              </div>
            </div>

            <div style="padding: 5px 10px;height: 100px;width: 90%;background-color:white;margin: 6px 10px;border-bottom: 1px solid #DDDDDD" v-else-if="showlist[index]">

            <div style="float: left;width: 20%"><img :src="ii.goodsrc" alt="" width="100%" style="min-width: 20%;max-width: 100px"></div>

            <div style="width: 75%;float: right;padding-left: 5px">
              <div style="float: left;width: 70%">
                <div style="font-size: 14px;">{{ii.goodname}}</div>
                <div style="background-color:#2CB12C;width: 80px;font-size: 14px;color: white;text-align: center;border-radius: 5px;margin-top: 5px" v-if="ii.goodlb1.length<5">{{ii.goodlb1}}</div>
              </div>
              <div style="float: right;width: 28%;padding-right: 2%">
                <div style="font-size: 12px;">¥<span style="font-size:18px ">{{ii.goodprice}}</span></div>
                <div style="color:#999999;font-size: 14px;">×1</div>
              </div>
            </div>
          </div>

        </div>
        <div style="text-align: center;font-size: 14px;color: #999999;height: 20px" @click="showit(index)" v-if="i.ikun.length>3&&!showlist[index]">-------显示更多----------</div>
       </div>

      </div>
    </div>

  </div>

</div>
</template>

<script>
import {Toast} from "vant";

export default {
  name: "shophistory",
  data(){
    return{
     user:{},
     historydd:[],
     show:false,
     gujia:true,
     showlist:[]

    }
  },
  created() {
    this.init_user()
  },
  mounted() {
    this.check_user()
    this.init_his()
  },
  methods:{
    init_user(){
      this.user = this.$store.state.user
      // if (this.$store.state.this_buought_goods_list.length<1){
      //   this.$router.back()
      // }
    },
    onClickLeft(){
      this.$router.back()

    },
    init_his(){
      this.$http.get('/goods/dingdan/history/'+this.user.id).then(res=>{
        this.historydd = res.data.data
        // for (let i = 0; i < this.historydd.length; i++) {
        //   this.historydd[i].show = false
          // this.historydd[i].dingdanid = this.historydd[i].dingdanid.slice(0,4)
        // }
        this.showlist = new Array(this.historydd.length)
        for (let i = 0; i < this.showlist.length; i++) {
          this.showlist[i] = false
        }
        // console.log(this.showlist)
        this.gujia = false
      }).catch(res=>{
        Toast.fail("服务器错误")
      })
    },
    showit(index){
      console.log(this.showlist[index])
      this.show = !this.show
      // this.showlist[index] = !this.showlist[index]
      this.$set(this.showlist,index,true)
    },

    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        this.$router.push('/login')
        return false
      }
      return true
    },
  }
}
</script>

<style scoped lang="less">
.colon {
  display: inline-block;
  margin: 0 4px;
  color: #ee0a24;
}
.block {
  display: inline-block;
  width: 22px;
  color: #fff;
  font-size: 12px;
  text-align: center;
  background-color: #fd4736;
}
</style>
