<template>
  <div class="hello">
    <div class="qian">

    </div>
    <img :src="imgarrhaha[indexx.ind]" style="width: 100%">

    <van-form @submit="onSubmit" class="mylogin">
      <van-field
          v-model="loginform.username"
          name="username"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
          v-model="loginform.userpwd"
          type="password"
          name="userpwd"
          label="密码"
          placeholder="密码"
          :rules="[{ validator, message: '密码必须有字母或数字且在六到十八位之间' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="onSubmit">提交</van-button>
      </div>
    </van-form>
    <div class="hou">
      <router-link style="font-size: 15px;height: 20px;line-height: 20px;float: right;color: #1989fa;margin-bottom: 5px" to="/regist" >没有账号？注册一个</router-link>
      <van-field
          v-model="yzm"
          center
          clearable
          label=""
          placeholder="请输入验证码"
      >
        <template #button v-if="showtime">
          <van-button size="small" type="primary" @click="getcode">获取验证码</van-button>
        </template>
      </van-field>
      <img :src="mysrc" style="width: 100%" v-if="showcheck">
    </div>

    <slot name="icon" :text="user"></slot>

  </div>
</template>

<script>
import {Toast} from "vant";
// import sa from "@/assets/imgs/wty163.jpg"

export default {
  name: 'Login',
  data() {
    return{
      code:".jpg",
      mysrc:"yzm-1673171985020.png",
      showtime:true,
      yzm:'',
      showcheck:false,
    loginform:{
      username:'',
      userpwd:''
    },
    user:{
    },
    imgarrhaha:[
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/199/199-bigskin-7.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/109/109-bigskin-9.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/176/176-bigskin-5.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/179/179-bigskin-4.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/111/111-bigskin-4.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/174/174-bigskin-6.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/508/508-bigskin-6.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/191/191-bigskin-7.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/152/152-bigskin-7.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/127/127-bigskin-7.jpg",
    ],
    indexx:{
        ind:1
    }
    // article_content:[]
    }
  },
  created() {
   this.indexx.ind =Math.floor(Math.random()*10)
  },
  mounted() {
   this.init()
  },
  methods: {
    onSubmit(values) {
      if(this.yzm.length<1){
        Toast.fail('请输入验证码')
        return false
      }
      this.$http.get('/articles/userscode/'+this.loginform.username+'/'+this.yzm).then(res=>{
        if (res.data.flag){
          this.user = res.data.data
          this.$http.get('/articles/users/'+this.loginform.username+'/'+this.loginform.userpwd).then(res=>{
            if (res.data.flag){
              this.user = res.data.data
              this.$store.commit('inituser',this.user)

              // console.log(this.user)
              // console.log(this.$store.state.user)
              window.sessionStorage.setItem('myboke-user',"youaaaa")
              this.$router.push('/shopclass')
            }
            else {
              Toast.fail('登录失败')
              this.loginform.userpwd = ''
            }
          }).catch((res) =>{
            Toast.fail('服务器状态异常！请稍后再试！')
          })
        }
        else {
          Toast.fail('验证码错误！')
          this.yzm = ''
        }
      }).catch((res) =>{
        Toast.fail('服务器状态异常！请稍后再试！')
      })

    },
    init(){
      var height = window.innerHeight
      var width = window.innerWidth
// console.log(height)
// console.log(width)
      var body = document.querySelector('.hello')
// console.log(body)
      body.style.height = height+"px"
      body.style.width = width+"px"

      this.$store.commit('set_artcle',[])
      this.$store.commit('set_user_artcle',[])
    },
    validator(val) {
      return /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z\\W]{6,18}$/.test(val);
    },
    getcode(){
      // this.code = "C://Users//Administrator//Desktop//yzm//yzm-1673171985020.png"
      // this.$store.commit('setlogin_code',this.code)
      // this.mysrc = "yzm-1673171985020.png"
      // console.log(this.mysrc)
      // this.showcheck = true

      if (this.loginform.username.length <1 || this.loginform.userpwd.length<1){
        Toast.fail('请填写用户名密码')
      }else {
        this.$http.post('/articles/users/code/'+this.loginform.username).then(res=>{
          // // 获取当前毫秒数，不刷新页面或不调用其他函数，这个值不会变化
          // let start = new Date().getTime();
          // /* 两次的时间不会差太多程序执行顺序太快基本不会变，
          // 当前时间小于开始时间加上3000毫秒，就执行后面花括号里的代码，
          // 这里是continue，可以写 {} ，也可以写 continue(执行到这就会跳出本次循环继续下次) */
          // while (new Date().getTime() < start + 3000) continue
          // console.log("以上代码阻塞了浏览器渲染3s，上面的代码执行完毕后，浏览器才会继续渲染页面");

          // this.mysrc = "yzm-1673171985020.png"

          this.mysrc = "/yzm/"+res.data.msg
          // this.showtime = false


          // console.log(this.mysrc.split("yzm")[1])
          // this.mysrc = "yzm"+this.mysrc.split("yzm")[1]
          // console.log(this.mysrc)


          Toast.success('获取成功！')
        }).finally(res =>{
          this.showcheck = true
        })

      }

    }

  }
}
// console.log('submit', values);
// this.$router.push('/home')
