<template>
  <div class="guess-container">
    <van-dialog v-model="show"
                :title="money"
                show-cancel-button
                @confirm="confirm"
                @cancel="cancel"
                :before-close="onBeforeClose">
      <div style="text-align: center;font-size: 15px;color: #94a5c0">你的余额为:{{user.surplus}}</div>
      <van-field v-model="number" type="number" placeholder="输入投注数量"/>
    </van-dialog>
    <van-dialog v-model="show1"
                :title="money"
                show-cancel-button
                @confirm="confirm2"
                @cancel="cancel"
                :before-close="onBeforeClose">
      <div style="text-align: center;font-size: 15px;color: #94a5c0">你的余额为:{{user.surplus}}</div>
      <van-field v-model="number" type="number" placeholder="输入投注数量"/>
    </van-dialog>
    <van-nav-bar
        title="无敌投注"
    />
    <van-notice-bar
        left-icon="volume-o"
        text="小船静静往返,马谛斯的海岸,星空下的夜晚,交给梵谷点燃,梦美的太短暂,孟克桥上呐喊,这世上的热闹,出自孤单"
    />
    <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="initguess"
    >
      <van-cell v-for="(i,index) in guess" :key="i.id"  class="mycell">
			  <span class="cell-left">
          <span class="limg">
			          <van-image
                    round
                    width="4rem"
                    height="4rem"
                    :src="src"
                />
            </span>
			  <span class="team-name">{{i.leftteam}}</span>

          <span class="lbtnfather">
			  <van-button plain hairline type="info" @click="buy(index)">{{i.leftrate}}</van-button>
         </span>
			  </span>

        <span class="cell-right">
          <span class="rimg">
			          <van-image
                    round
                    width="4rem"
                    height="4rem"
                    src="https://img01.yzcdn.cn/vant/cat.jpeg"
                />
          </span>
			  <span class="team-name">{{i.rightteam}}</span>
        <span class="rbtnfather">
			      <van-button plain hairline type="info" @click="buy2(index)">{{i.rightrate}}</van-button>
			  </span>
      </span>

      </van-cell>
    </van-list>
    <Tab></Tab>
  </div>

</template>

<script>
import {Toast} from 'vant'
import Tab from './peihe/Tab'
export default {
  name: 'Guess',
  components:{
    Tab
  },
  data() {
    return{
      loading: false,
      finished: false,
      show: false,
      show1:false,
      isclose:true,
      number:"",
      money:"",
      selectedid:0,
      guess:[],
      user:{
        name:'',
        id:0,
        username:'',
        surplus:0
      },
      src:require('../assets/imgs/headico/user-5.jpg')
    }
  },
  created() {
  this.init_user()
  },
  mounted() {
  this.check_user()
  },
  methods: {
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 10) {
          this.finished = true;
        }
      }, 500);
    },
    buy(index){
      this.show = true
      // console.log(this.guess[index])
      this.money =  "进行投注："+this.guess[index].leftteam
      this.selectedid = this.guess[index].id
    },
    buy2(index){
      this.show1 = true
      // console.log(this.guess[index])
      this.money =  "进行投注："+this.guess[index].rightteam
      this.selectedid = this.guess[index].id
    },
    cancel(){
      this.number = ""
      Toast('取消');
    },
    confirm(){
      if (this.user.surplus>=this.number  && this.number > 0) {
        this.$http.post("/mesdatas/guess/l" + "/" + this.number + "/" + this.selectedid + "/" +this.user.id).then(res => {
          Toast.success('投注成功！');
          this.user.surplus -= this.number
          this.initguess()
          this.number = ""
        }).catch((res) => {
          Toast.fail('服务器异常投注失败！');
        })
        this.isclose = true
      }else {
        Toast.fail('余额不够请重新输入！');
        this.isclose = false
        this.number = ""
      }
    },
    confirm2(){
      if (this.user.surplus>=this.number && this.number > 0) {
        this.$http.post("/mesdatas/guess/r" + "/" + this.number + "/" + this.selectedid + "/" +this.user.id).then(res => {
          Toast.success('投注成功！');
          this.user.surplus -= this.number
          this.initguess()
          this.number = ""
        }).catch((res) => {
          Toast.fail('服务器异常投注失败！');
          this.number = ""
        })
        this.isclose = true
      }else {
        Toast.fail('余额不够请重新输入！');
        this.isclose = false
        this.number = ""
      }

    },
    initguess(){
      this.$http.get("/mesdatas/guess").then(res=>{
        this.loading = false;
        this.guess = res.data.data

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.guess.length >= this.guess.length) {
          this.finished = true;
        }
        // console.log(this.guess)
      })
      // this.user = this.$session.get("user")
      // console.log(this.user.surplus)
    },
    async onBeforeClose(action, done) {
      if (action === "confirm") { // 点击确定走这里

        if (this.isclose) {
          // 登录成功
          done(true)
        } else {
          // 登录失败，阻止弹窗关闭
          // done()里可以放true和false来控制
          done(false) // 阻止弹窗关闭
        }
      } else { // 点击取消走这里
        done(true) // 关闭弹窗, true可以省略
      }
    },
    init_user() {
      this.user = this.$store.state.user
    },
    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        this.$router.push('/login')
        return false
      }
      return true
    }
  }
}
</script>

<style lang="less" scoped>
.guess-container{
  min-width: 320px;

}
.van-nav-bar{
  background-color:#F47E60;
}
.van-list .mycell{
  background-color:pink;
  height: 230px;
}
.van-cell__value {
  padding: 0 10px 0 10px;
  display: flex;
  justify-content: space-between;

}
.cell-left {
  flex: 1;
  position: relative;
  width: 50%;
}
.cell-right{
  flex: 1;
  position: relative;
  width: 50%;
}
.lbtnfather{
  position: absolute;
  top: 80%;
  width: 100%;
  display: flex;
  justify-content: center;
}
.rbtnfather{
  position: absolute;
  top: 80%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.limg{
  position: absolute;
  top: 16%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.rimg{
  position: absolute;
  top: 16%;
  width: 100%;
  display: flex;
  justify-content: center;
}


.team-name{
  position: absolute;
  width: 100%;
  text-align: center;
  top: 60%;
  //right: 45%;
  font-size: 20px;
  font-weight: bold;
}

</style>
