<template xmlns="http://www.w3.org/1999/html">
<div ref="scrollRef">

<returntop></returntop>
<!--  头部导航模块-->
  <van-nav-bar title="详情" left-text="返回" left-arrow @click-left="click_left"
               :fixed=true style="background-color:orange;">
  </van-nav-bar>
  <div style="margin-top: 46px"></div>

<!--  文章模块-->
  <div>
    <h2 style="padding: 2px 15px 0 15px">{{article.title}}</h2>

<!--    文章作者信息-->
    <div style="padding-left: 10px">
      <div style="height: 50px">
        <img :src=article.writerhead alt="" style="float: left;width: 45px">
        <div style="float: left;margin-left: 10px;width: 80%">作者：{{article.writer}}</div>
        <div style="float: left;margin-left: 10px">来自：{{article.writerfrom}}</div>
      </div>
      <div style="text-align: center; font-size: 12px;color: #A2A2A2">{{article.createtimedetail}}</div>
    </div>

<!--    文章内容-->
    <div style="padding: 0 20px 0 20px;margin-top: 20px;">
      <div v-html="article.content" class="markdown-body"></div>
    </div>

<!--    文章点赞模块-->
    <div style="height: 70px;margin-top: 20px;display: flex;justify-content: center;">
      <img src="../../assets/imgs/dianzan.png"
           alt="" style="height: 80px;width: 80px"
           @click="dianzan"
      >
    </div>
    <div style="text-align: center;margin-top: 5px">{{article.dianzancount}}</div>
  </div>

<!--  评论展示模块-->
  <div style="margin-top: 50px;">
<!--    最新 or 最热-->
      <div style="height: 30px;background-color:orange;margin-bottom: 10px">
        <div style="font-size: 12px;line-height: 30px;
        float: left;padding-left: 5px">
          {{comment_select.left}}
        </div>

        <div style="float: right;font-size: 12px;
        line-height: 30px;padding-right: 5px"
        @click="comment_change">
          <van-icon name="points"/>
          {{comment_select.right}}
        </div>
      </div>

      <van-list
      v-model="loading"
      :finished="finished"
      :offset="50"
      finished-text="没有更多评论了"
      @load="onLoad"
      style="margin-bottom: 60px;"
  >
        <van-cell v-for="(item,index) in comment_content" :key="index">
          <div style="min-height: 120px;">
            <img :src=item.userheadico alt="" style="width: 45px;float: left">

            <div style="float: left;margin-left: 12px;width: 80%">
              <div style="float: left;color: #60656c;font-size: 13px">{{item.username}}</div>

              <div style="float: right;">
                <div style="float: right;">{{item.dianzanshu}}</div>
                <van-icon name="good-job-o" @click="comment_dianzan(item)"
                          style="float: right;margin-right: 10px;line-height: 24px"/>
              </div>

            </div>

            <div style="width: 95%;float: left;margin-bottom: 20px;margin-left: 12px;margin-top: 5%;min-height: 60px;font-size: 16px;">
              {{item.comment}}
            </div>

            <div>
              <div style="float: left;font-size: 10px;color: #A2A2A2">{{item.time}}</div>
              <div style="float: left;font-size: 10px;margin-left: 5px;color: #A2A2A2">来自：{{item.userfrom}}</div>
              <span style="float: right;margin-right: 40px">回复</span>
            </div>

          </div>
        </van-cell>
  </van-list>
  </div>

<!--  输入评论模块-->
  <van-tabbar>
    <van-field
        v-model="the_comment.comment"
        center
        clearable
        label="留言"
        placeholder="请输入你的评论"

    >
      <template #button>
        <van-button size="small" type="primary" @click="submit_comment">提交</van-button>
      </template>
    </van-field>
  </van-tabbar>

</div>
</template>

<script>
import {Toast} from "vant";
import returntop from "@/components/peihe/Returntop";

export default {
  name: "articledetail",
  components:{
    returntop
  },
  data() {
    return{
      user:{
        name:'',
        id:0,
        username:'',
        surplus:0
      },
      article: {
      },
      the_comment:{
        id:"",
        time: "",
        userheadico: "",
        username: "",
        userid:"",
        articleid: 1,
        comment: "",
        createtime: 1669339011,
        userfrom: "",
        dianzanshu: 0
      },
      loading: false,
      finished: false,
      now_selected:0,
      comment_select_xuan:[
        {left:'最热评论',right:'最新'},
        {left:'最新评论',right:'最热'}
      ],
      comment_select:{
        left:'最热评论',
        right:'最新'
      },
      comment_content:{}
    }
  },
  created() {
   this.initit()
  },
  mounted() {
  // this.onLoad()
  },
  methods: {
    initit(){
      this.user = this.$store.state.user
      this.article.id = this.$store.state.selected_articleid
      this.$http.get('/articles/'+this.article.id).then(res=>{
        this.article = res.data.data
      }).catch((res) => {
        Toast.fail('服务器异常获取失败！');
      })
    },
    click_left(){
      this.$router.back()
    },
    submit_comment(){
      if (this.the_comment.comment.length>=1){
        this.the_comment.articleid = this.article.id
        this.the_comment.userid = this.user.id
        this.the_comment.username = this.user.name
        this.the_comment.userfrom = this.user.userfrom
        this.the_comment.userheadico = this.user.headico
        this.$http.post("/articles/comments",this.the_comment).then(res => {
          this.the_comment.comment = ""
          this.onLoad()
          Toast.success("评论成功！")
        }).catch(res =>{
          Toast.fail("服务器错误！稍后再试！")
        })
      }else {
        Toast.fail("输入不能为空")
      }
    },
    onLoad() {
      this.$http.get('/articles/comments/'+this.article.id).then(res=>{
        this.comment_content = res.data.data
        // console.log(this.comment_content)
      }).catch((res) => {
        Toast.fail('服务器异常获取失败！');
      }).finally((res) =>{
        this.loading = false
        this.finished = true
      })
    },
    zuixinonload(){
      this.$http.get('/articles/comments/xin/'+this.article.id).then(res=>{
        this.comment_content = res.data.data
        console.log(this.comment_content)
      }).catch((res) => {
        Toast.fail('服务器异常获取失败！');
      }).finally((res) =>{
        this.loading = false
        this.finished = true
      })
    },
    dianzan(){
      this.article.dianzancount++
      this.$http.post('/articles/dianzan/'+this.article.dianzancount+'/'+this.article.id).then(res=>{
      console.log(res.data)
      }).catch((res) => {
        Toast.fail('服务器异常点赞失败！');
      })
    },
    comment_change(){
      if (this.now_selected===0) {
        this.now_selected = 1
        this.comment_select = this.comment_select_xuan[1]

        this.comment_content = []
        this.loading = true
        this.finished = false
        this.zuixinonload()
      }else{
        this.now_selected = 0
        this.comment_select = this.comment_select_xuan[0]

        this.comment_content = []
        this.loading = true
        this.finished = false
        this.onLoad()
      }
    },
    comment_dianzan(item){
      // console.log(item.id)
      // console.log(item.dianzanshu)
      item.dianzanshu ++
        this.$http.post("/articles/comments/dianzan/"+item.dianzanshu+'/'+item.id).then(res => {
        console.log(res.data)
        }).catch(res =>{
          Toast.fail("服务器错误！稍后再试！")
        })
    }
  }
}
</script>

<style scoped lang="less">


</style>
