<template>
  <div class="hello">
    <van-nav-bar title="创作" left-text="返回" left-arrow @click-left="click_left"
                  :fixed="false" style="background-color:#FFBB55;">
    </van-nav-bar>
<!--    <div style="min-height: 46px"></div>-->
    <div style="height: 10%;margin-top: 10px">
      <van-form>
        <van-field
            v-model="form.title"
            type="text"
            style="font-size: 20px;font-weight: bold"
            placeholder="输入文章标题"
            :rules="[{ validator, message: '文章标题不能为空且大于八个字符小于五十个字符' }]"
        />
      </van-form>
    </div>

<!--    :value="detailContent.content"：引入要转换的内容-->
<!--    :subfield = "false"开启单栏模式-->
<!--    :defaultOpen = "'preview'"：默认展示预览区域-->
<!--    :toolbarsFlag = "false"：关闭工具栏-->
<!--    :editable="false"：不允许编辑-->
<!--    scrollStyle="true"：开启滚动条样式(暂时仅支持chrome)-->
<!--    :ishljs = "true"：开启代码高亮-->

    <mavon-editor  @change="change" :subfield = "false" :external-link="externalLink"
                   v-model="form.content" style="height: 70%;width: 100%" :toolbars="toolbars"/>

    <div style="border: 1px solid #FF7F00;border-radius: 10px; margin-top: 20px;padding: 10px 10px 10px 10px">
      <van-radio-group v-model="radio" style="" direction="horizontal">
        <div style="margin-right: 10px">封面样式：</div>
        <van-radio name="1" checked-color="#ee0a24">单图模式</van-radio>
        <van-radio name="2" checked-color="#ee0a24">三图模式</van-radio>
      </van-radio-group>
    </div>

    <div style="border: 1px solid #FF7F00;border-radius:10px;margin-top: 20px;padding: 3px">
      <van-cell-group v-if="radio==='1'">
        <van-field required v-model="commit_content.fm1" label="图片地址" placeholder="图片地址" />
      </van-cell-group>

      <van-cell-group v-if="radio==='2'">
        <van-field required v-model="commit_content.fm1" label="图片地址" placeholder="图片地址" />
        <van-field required v-model="commit_content.fm2" label="图片地址" placeholder="图片地址" />
        <van-field required v-model="commit_content.fm3" label="图片地址" placeholder="图片地址" />
      </van-cell-group>
    </div>

    <van-button type="primary" round block style="min-width: 80px;float: right;margin-top: 10px" @click="submit">提交</van-button>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import "mavon-editor/dist/css/index.css";
import {Toast} from "vant";


export default {
  name: "articledetail",
  components:{
    mavonEditor,
  },
  data() {
    return{
      user:{
      },
      form:{
        title:'',
        content:'',
        zhuanhua:'',
      },
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        // mark: true, // 标记
        // superscript: true, // 上角标
        // subscript: true, // 下角标
        // quote: true, // 引用
        // ol: true, // 有序列表
        // ul: true, // 无序列表
        // link: true, // 链接
        // imagelink: true, // 图片链接
        // code: false, // code
        // table: true, // 表格
        // fullscreen: true, // 全屏编辑
        // readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        // help: true, // 帮助
        // /* 1.3.5 */
        // undo: true, // 上一步
        // redo: true, // 下一步
        // trash: true, // 清空
        // save: true, // 保存（触发events中的save事件）
        // /* 1.4.2 */
        // navigation: true, // 导航目录
        // /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        // /* 2.2.1 */
        // subfield: true, // 单双栏模式
        preview: true, // 预览
      },
      codeStyle:'monokai-sublime',//主题
      externalLink: {
        markdown_css: false,
        hljs_js: () => '/md/highlightjs/highlight.min.js',
        hljs_css: (css) => '/md/highlightjs/styles/' + css + '.min.css',
        hljs_lang: (lang) => '/md/highlightjs/languages/' + lang + '.min.js',
        katex_css: () => '/md/katex/katex.min.css',
        katex_js: () => '/md/katex/katex.min.js',
      },
      commit_content:{
        title:'',
        content:'',
        commentcount:0,
        createtime:0,
        time:'',
        dianzan:0,
        writerid:-1,
        writer:'',
        writerhead:'',
        writerfrom:'',
        type:0,
        fm1:'',
        fm2:'',
        fm3:''
      },
      radio: '1',
    }
  },
  created() {

  },
  mounted() {
    this.init_height()
    this.check_user()
  },
  computed: {

  },
  methods: {
    click_left(){
      this.$router.back()
    },
    change(value,render){
      // var htmlContent = this.converter.makeHtml(this.form.content)
      // console.log(render)
      this.form.zhuanhua = render
    },
    init_height(){
      var height = window.innerHeight
      var width = window.innerWidth
      var body = document.querySelector('.hello')
      body.style.height = height + "px"
      body.style.width = width + "px"
      this.user = this.$store.state.user
    },
    validator(val) {
      return val.length < 50 && val.length >8;
    },
    submit(){
      // console.log(this.user)
      // console.log(this.check_user())
      // this.form.zhuanhua = render
      if (this.form.title.length < 50
          && this.form.title.length >8
          && this.form.content.length!==0
          && this.commit_content.fm1.length>5
      ){
         this.commit_content.title = this.form.title
         this.commit_content.content = this.form.zhuanhua
         this.commit_content.writer = this.user.name
         this.commit_content.writerid = this.user.id
         this.commit_content.writerhead = this.user.headico
         this.commit_content.writerfrom = this.user.userfrom
         this.commit_content.type = this.radio === '1'?0:1

        this.$http.post('/articles',this.commit_content).then(res=>{
          this.form = {}
          this.commit_content.fm1 = ""
          this.commit_content.fm2 = ""
          this.commit_content.fm3 = ""
          this.user.wzcount++
          this.$http.post('/articles/wzcount/'+this.user.wzcount+'/'+this.user.id).then(res=>{
            Toast.success('提交成功！');
          })
           this.$store.commit('set_artcle',[])
           this.$store.commit('set_user_artcle',[])
        }).catch((res) => {
          Toast.fail('服务器异常提交失败！');
        })
      }
      else {
        Toast.fail("检查输入！")
      }

    },
    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
          Toast.fail("请登录！")
          window.sessionStorage.clear()
          this.$router.push('/login')
          return false
      }
      return true
    }
  }
}

</script>

<style scoped lang="less">
//@import '/md/markdown/github-markdown.min.css';
.hello {
  /deep/ .van-cell__title{
    margin-right: 0px !important;
    width: 5.5em;
  }
}

</style>
