<template>
  <div class="hello">
    <div class="qian">

    </div>
    <img :src="imgarrhaha[indexx.ind]" style="width: 100%">

    <van-form @submit="onSubmit" class="mylogin">
      <van-field
          v-model="registform.username"
          name="username"
          label="邮箱地址"
          placeholder="邮箱地址"
          :rules="[{ validator:validator3, message: '请填写符合规则的邮箱地址' }]"
      />

      <van-field
          v-model="registform.userpwd"
          type="password"
          name="userpwd"
          label="密码"
          placeholder="密码"
          :rules="[{ validator:validator, message: '密码必须有字母或数字且在六到十八位之间' }]"
      />
      <van-field
          v-model="registform.userpwd2"
          type="password"
          name="userpwd2"
          label="再次输入密码"
          placeholder="再次输入密码"
          :rules="[{ validator:validator2, message: '必须与上次密码一致' }]"
      />

      <van-field
          v-model="yzm"
          name="yzm"
          label="验证码"
          placeholder="验证码"
          :rules="[{ required: true, message: '请填写验证码' }]"
          style="margin-top: 10px"
      >
        <template #button>
          <van-button size="small" type="primary" @click="getcode" :disabled="showtime">获取邮箱验证码
            <van-count-down :time="time" format=" ss 秒" style="color: white" v-if="showtime" @finish="showtime = !showtime"/>
          </van-button>
        </template>
      </van-field>

      <div style="margin: 16px;">
        <van-button round block type="info" native-type="onSubmit">注册</van-button>
      </div>
    </van-form>



    <slot name="icon" :text="user"></slot>

  </div>
</template>

<script>
import {Toast} from "vant";
// import sa from "@/assets/imgs/wty163.jpg"
Toast.setDefaultOptions({ duration: 3000 });

export default {
  name: 'Register',
  data() {
    return{
      code:".jpg",
      showtime:false,
      yzm:'',
      showcheck:false,
      registform:{
        username:'',
        userpwd:'',
        userpwd2:''
      },
      user:{
      },
      time: 60 * 1000,
      imgarrhaha:[
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/199/199-bigskin-7.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/109/109-bigskin-9.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/176/176-bigskin-5.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/179/179-bigskin-4.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/111/111-bigskin-4.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/174/174-bigskin-6.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/508/508-bigskin-6.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/191/191-bigskin-7.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/152/152-bigskin-7.jpg",
        "http://game.gtimg.cn/images/yxzj/img201606/skin/hero-info/127/127-bigskin-7.jpg",
      ],
      indexx:{
        ind:1
      }
    }
  },
  created() {
    this.indexx.ind =Math.floor(Math.random()*10)
  },
  mounted() {
    this.init()
  },
  methods: {
    onSubmit(values) {
      if(this.yzm.length<1 || this.registform.userpwd2.length<1){
        Toast.fail('请检查输入')
        return false
      }
      this.$http.get('/articles/users/registcode/'+this.registform.username+'/'+this.yzm).then(res=>{
        if (res.data.data){
          this.user = res.data.data

          this.$http.post('/articles/users/regist/'+this.registform.username+'/'+this.registform.userpwd).then(res=>{
            if (res.data.flag){
              Toast.success('注册成功！跳转登陆！')

              this.$http.get('/articles/users/'+this.registform.username+'/'+this.registform.userpwd).then(res=>{
                if (res.data.flag){
                  this.user = res.data.data
                  this.$store.commit('inituser',this.user)

                  // console.log(this.user)
                  // console.log(this.$store.state.user)
                  window.sessionStorage.setItem('myboke-user',"youaaaa")
                  this.$router.push('/shopclass')
                }
                else {
                  Toast.fail('登录失败')
                }
              })

            }
            else {
              Toast.fail('注册失败')
            }
          }).catch((res) =>{
            Toast.fail('服务器状态异常！请稍后再试！')
          })

        }
        else {
          Toast.fail('验证码错误！')
          this.yzm = ''
        }
      }).catch((res) =>{
        Toast.fail('服务器状态异常！请稍后再试！')
      })
    },
    init(){
      var height = window.innerHeight
      var width = window.innerWidth
// console.log(height)
// console.log(width)
      var body = document.querySelector('.hello')
// console.log(body)
      body.style.height = height+"px"
      body.style.width = width+"px"

      this.$store.commit('set_artcle',[])
      this.$store.commit('set_user_artcle',[])
    },
    validator(val) {
      return /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z\\W]{6,18}$/.test(val);
    },
    validator2(val) {

      return this.registform.userpwd === val;
    },
    validator3(val) {
      return /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/.test(val);
    },
    getcode(){
      this.showtime = true
      if (this.registform.username.length <1){
        Toast.fail('请填写用户名')
      }
      else {
        Toast("正在发送")
        this.$http.post('/articles/users/registcode/'+this.registform.username).then(res=>{
          if (res.data.data===true) {
            Toast.success('验证码发送成功！')
          }
          else{
            if (res.data.msg!=null){
              Toast.fail(res.data.msg)
            }else {
              Toast.fail("发送失败！")
            }
          }

          console.log(res.data)
        }).finally(res =>{

        })

      }

    }

  }
}

</script>


<style scoped lang="less">
.mylogin{
  //border: 1px black solid;
  padding-top: 20%;
}
.hello{
  background-color: orange;
  //height: 100%;
  //width: 100%;
}
.qian{
  //height: 40%;
  //padding-top: 40%;
}
.hou{
  //min-height: 200px;
  //height: 60%;
  padding-bottom: 60%;
}
</style>
