<template>
<div style="background-color:#f2f2f2;min-width: 320px;min-height: 800px">

  <van-nav-bar
      title="交易成功"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
      :fixed="true"
  />

   <div style="height: 50px"></div>
   <div style="box-sizing: border-box;">
     <div v-for="(i,index) in this_bought_list" :key="index" style="">

       <div style="padding: 5px 10px;height: 100px;width: 90%;background-color:white;margin: 6px 10px;border-radius: 15px">
          <div style="float: left;width: 20%"><img :src="i.goodsrc" alt="" width="100%" style="min-width: 20%;max-width: 100px"></div>

          <div style="width: 75%;float: right;padding-left: 5px">
            <div style="float: left;width: 70%">
              <div style="font-size: 14px;">{{i.goodname}}</div>
              <div style="background-color:#2CB12C;width: 80px;font-size: 14px;color: white;text-align: center;border-radius: 5px;margin-top: 5px" v-if="i.goodlb1.length<5">{{i.goodlb1}}</div>
            </div>
            <div style="float: right;width: 28%;padding-right: 2%">
              <div style="font-size: 12px;">¥<span style="font-size:18px ">{{i.goodprice}}</span></div>
              <div style="color:#999999;font-size: 14px;">×1</div>
            </div>
          </div>
     </div>

     </div>
     <div style="height: 150px;font-size: 14px;background-color:#fff;margin: 10px 5px;border-radius: 10px">
       <div style="margin: 0 5%">

         <div style="width: 100%;height: 30px;line-height: 30px;padding-top: 10px">
           <div style="float: left">收货信息：</div>
           <div style="float: right">{{this_dingdan.username}}</div>
         </div>

         <div style="width: 100%;height: 30px;line-height: 30px">
           <div style="float: left">订单号：</div>
           <div style="float: right">a54rer{{this_dingdan.dingdanid}}d4556</div>
         </div>

         <div style="width: 100%;height: 30px;line-height: 30px">
           <div style="float: left">共计：</div>
           <div style="float: right">¥ {{this_dingdan.allcount}}</div>
         </div>

         <div style="width: 100%;height: 30px;line-height: 30px">
           <div style="float: left">创建时间：</div>
           <div style="float: right">{{this_dingdan.time}}</div>
         </div>

       </div>


     </div>
   </div>
  <div style="height: 50px;"></div>

</div>
</template>

<script>
import {Toast} from "vant";

export default {
  name: "shopsuccess",
  data(){
    return{
    this_bought_list:[],
      this_dingdan:{}

    }
  },
  created() {
    this.init_user()

  },
  mounted() {
    this.check_user()
    this.init_dingdan()
  },
  methods:{
    init_user(){
      this.user = this.$store.state.user
      // if (this.$store.state.this_buought_goods_list.length<1){
      //   this.$router.back()
      // }
    },
    init_dingdan(){
      this.$http.get('/goods/dingdan/'+this.$store.state.this_buought_goods_id).then(res=>{
      this.this_dingdan = res.data.data
        console.log(this.this_dingdan);
        this.$http.get('/goods/dingdan/list/'+this.this_dingdan.goodidlist).then(res=>{
          this.this_bought_list = res.data.data
          // console.log(this.this_bought_list)
        })
      }).catch((res) => {
        Toast.fail('服务器异常获取失败！');
      })
    },

    onClickLeft(){
      this.$router.back()

    },
    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        this.$router.push('/login')
        return false
      }
      return true
    },

  }
}
</script>

<style scoped lang="less">

</style>
