<template>
<div>
  <div>
    <van-nav-bar title="我的文章" left-text="返回" left-arrow @click-left="click_left"
                 :fixed=true style="background-color:#FFBB55;">
    </van-nav-bar>

    <Returntop></Returntop>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
          style="margin-top: 46px"
      >
        <van-cell v-for="(item,index) in article_content" :key="index" @click="click_detail(index)">
          <div v-if="item.type">
            <div style="font-weight: bold;font-size: 20px">{{item.title}}</div>

            <div style="min-height: 50px;display: flex;justify-content: space-between;margin-top: 5px">
              <img :src=item.fm1 referrerpolicy="no-referrer" style="width: 30%;">
              <img :src=item.fm2 referrerpolicy="no-referrer" style="width: 30%;">
              <img :src=item.fm3 referrerpolicy="no-referrer" style="width: 30%;">
            </div>
            <div style="color:#9499a0;font-size: 12px;margin-top: 5px">
              <span style="margin-right: 5px">{{item.writer}}</span>
              <span style="margin-right: 5px">{{item.commentcount}}条评论</span>
              <span>{{item.createtimedetail}}</span>
            </div>
          </div>

          <div v-else>
            <div>
              <span style="font-weight: bold;font-size: 20px;width: 65%;float: left;height: 100px">{{item.title}}</span>
              <span style="">
              <img :src=item.fm1 referrerpolicy="no-referrer" style="width: 30%;float: right">
            </span>
            </div>

            <div style="color:#9499a0;font-size: 12px;margin-bottom: 0px;float: left;width: 100%">
              <span style="margin-right: 5px">{{item.writer}}</span>
              <span style="margin-right: 5px">{{item.commentcount}}条评论</span>
              <span>{{item.createtimedetail}}</span>
            </div>
          </div>

        </van-cell>

      </van-list>
    </van-pull-refresh>


  </div>
</div>
</template>

<script>
import Returntop from "@/components/peihe/Returntop";
import {Toast} from "vant";
export default {
  name: "articleuser",
  components: {
    Returntop
  },
  data() {
    return{
      user:{
        name:'',
        id:0,
        username:'',
        surplus:0
      },
      loading: false,
      finished: false,
      refreshing: false,
      type:false,
      src:{
        o:'https://game.gtimg.cn/images/yxzj/img201606/heroimg/154/154-smallskin-5.jpg',
        t:'https://game.gtimg.cn/images/yxzj/img201606/heroimg/174/174-smallskin-3.jpg',
        s:'https://game.gtimg.cn/images/yxzj/img201606/heroimg/174/174-smallskin-6.jpg'
      },
      article_content:[
      ]
      // {id:1,title:"这是标题？！这是标题？！这是标题？！标题？！这是标题？！这是标题？标题？！这是标题？！这是标题？",content:"",writer:"坤只因",time:"2028-08-09",commentcount:258,type:true},
      // {id:2,title:"这是标题？！这是标题？！这是标题？！标题？！这是标题？！这是标题？",content:"",writer:"坤只因",time:"2028-08-09",commentcount:258,type:false},
      // {id:3,title:"这是标题？！这是标题？！这是标题？！标题？！这是标题？！这是标题？",content:"",writer:"坤只因",time:"2028-08-09",commentcount:258,type:true},
      // {id:4,title:"这是标题？！这是标题？！这是标题？！标题？！这是标题？！这是标题？",content:"",writer:"坤只因",time:"2028-08-09",commentcount:258,type:true},
      // {id:5,title:"这是标题？！这是标题？！这是标题？！标题？！这是标题？！这是标题？标题？！这是标题？！这是标题？",content:"",writer:"坤只因",time:"2028-08-09",commentcount:258,type:false},
    }
  },
  created() {
    this.init_user()
    this.init_article()
  },
  mounted() {
    this.check_user()
  },
  methods:{
    init_user() {
      this.user = this.$store.state.user
      // console.log(this.user)
    },
    click_left(){
      this.$router.back()
    },
    start_init_article(){
      this.$http.get('/articles/my/'+this.user.id).then(res=>{
        this.article_content = res.data.data
        // console.log(this.article_content)
        for (let i = 0; i < this.article_content.length; i++) {
          // console.log(this.article_content[i]);
          this.article_content[i].createtimedetail = this.article_content[i].createtimedetail.split(" ")[0]
          if (this.article_content[i].fm1 === null){
            this.article_content[i].fm1 = this.src.o
            this.article_content[i].fm2 = this.src.t
            this.article_content[i].fm3 = this.src.s
          }
        }
        this.$store.commit('set_user_artcle',this.article_content)
      }).catch((res) => {
        Toast.fail('服务器异常获取失败！');
      }).finally((res) =>{
        this.loading = false
        this.finished = true
      })
    },
    init_article(){
      if (this.$store.state.this_user_article.length === 0){
        this.start_init_article()
      }else {
        this.article_content = this.$store.state.this_user_article
        this.loading = false
        this.finished = true
      }
    },
    onLoad() {
        if (this.refreshing) {
          this.article_content = [];
          this.refreshing = false;
          this.start_init_article()
        }
        for (let i = 0; i < this.$store.state.this_user_article.length; i++) {
          this.article_content.push(this.$store.state.this_user_article[i]);
        }
        // this.article_content.push(this.article_content.length + 1);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;
      this.$store.state.this_user_article = []

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    click_detail(index){
      // console.log(this.article_content[index])
      this.$store.commit('set_articleid',this.article_content[index].id)
      this.$router.push('/detail')
    },
    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        this.$router.push('/login')
        return false
      }
      return true
    },
  }
}
</script>

<style scoped lang="less">

</style>
