// src/router/index.js 就是当前项目的路由模块
import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuesession from "vue-session";
import VueCookies from "vue-cookies";

// 导入需要的组件
import Home from '@/components/Home'
import Video from '@/components/Video'
import User from '@/components/User'
import Guess from '@/components/Guess'
import Login from "@/components/Login";
import detail from "@/components/child/articledetail";
import write from "@/components/child/articlewrite";
import search from "@/components/child/articlesearch";
import articleuser from "@/components/child/articleuser";
import searchreturn from "@/components/child/searchreturn";
import shopclass from "@/components/shop/shopclass";
import shopjiesuan from "@/components/shop/shopjiesuan";
import shopsuccess from "@/components/shop/shopsuccess";
import shophistory from "@/components/shop/shophistory";
import Register from "@/components/Register";

// 把 VueRouter 安装为 Vue 项目的插件
// Vue.use() 函数的作用，就是来安装插件的

Vue.use(VueRouter)



// 创建路由的实例对象
const router = new VueRouter({
  mode:'hash',
  // routes 是一个数组，作用：定义 “hash 地址” 与 “组件” 之间的对应关系
  routes: [
  { path: '/', component: Home,},
  { path: '/home', component: Home,},
  // 定义我的路由规则
  { path: '/user', component: User},
  { path: '/video', component: Video,},
  { path: '/Guess', component: Guess,},
  { path: '/detail', component: detail,},
  { path: '/write', component: write,},
  { path: '/search', component: search,},
  { path: '/articleuser', component: articleuser},
  { path: '/searchreturn', component: searchreturn},

  { path: '/shopclass', component: shopclass},
  { path: '/shopjiesuan', component: shopjiesuan},
  { path: '/shopsuccess', component: shopsuccess},
  { path: '/shophistory', component: shophistory},

  { path: '/login', component: Login,},
  { path: '/regist', component: Register,},
  ]
})

// 路由拦截器
router.beforeEach(async (to, from, next) => {
  // console.log(to)
  // console.log(from)
  // console.log(to.path)
  // if (to.path != '/login') { // 判断该路由是否需要登录权限
  //   let token = localStorage.getItem('token');
  //   if (token) { // 获取当前的 token 是否存在
  //     next()
  //   } else {
  //     // 不存在 token，需要重新认证
  //     next({
  //       path: '/#/home',
  //       query: {
  //         redirect: to.fullPath
  //       }
  //     })
  //   }
  // }

if (to.path !== '/login'&& to.path !=='/shopclass'&& to.path !=='/regist'){
    // console.log(VueCookies.get("myboke-user"))
    // if (VueCookies.get("myboke-user")==null){
    //   next({
    //     path: '/login',
    //     query: {
    //       redirect: to.fullPath
    //     }
    //   })
    // }

    // console.log("拦截器生效！")
    // console.log('获取到session   '+ sessionStorage.getItem("myboke-user"));
    if (sessionStorage.getItem("myboke-user")==null){
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
      return false
    }
  }
  next();
});

// 解决上一个页面跳转然后不另外一个页面不滚动的问题
router.afterEach((to) => {
  if (to.path !== './home' || to.path !== './'){
    window.scrollTo(0, 0);
    document.querySelector("body").setAttribute("style", "overflow: auto !important;");
  }
});

export default router
