<template>
<div style="background-color:#EEEEEE;min-width: 300px;min-height: 800px">
  <van-nav-bar
      title="结算界面"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
      :fixed="true"
  />
  <div style="height: 50px"></div>

  <div style="height: 50px;background-color:#fff;border-radius: 10px">
    <div style="height: 100px;">
      <div style="width: 70%;float: left;line-height: 50px;font-size: 15px;margin-left: 5px">共计{{all_count}}样物品，需¥<span style="color: red;font-size: 20px">{{all_price}}</span></div>
      <div style="float: right">
        <van-button color="#7232dd" plain round @click="frqk" style="margin-top: 3px">清空</van-button>
      </div>
    </div>
  </div>

  <div v-for="(i,index) in xuanze" :key="index" style="padding: 0 10px;">
    <div style="margin-top: 10px;" v-if="i.child.length>0">

      <div style="background-color:#fff;border-radius: 15px;">
        <div style="border-bottom: 1px solid #C4C4C4;font-size: 16px;font-weight: bold;margin-left: 5px;height: 45px;line-height: 45px;" >{{i.name}}</div>
        <div v-for="(item,indexx) in i.child" :key="indexx">

          <div style="margin-top: 10px;padding: 10px;box-sizing: border-box;">

            <div style="border-bottom: 1px solid red;">
            <img :src=item.goodsrc alt="" width="40%" style="min-width: 40%;margin-right: 2%" >
            <div style="float: right;width: 58%">
              <div style="width: 90%; overflow: hidden;white-space: nowrap;text-overflow: ellipsis;font-size: 14px;height: 20px;font-weight: bold;line-height: 20px">{{item.goodname}}</div>

              <div style="color:#ff2078;font-size: 12px;margin-top: 10px;height: 20px">
                <span style="background-color:#ffeff5;border-radius: 5px">&nbsp;&nbsp;{{item.goodgrade}}分&nbsp;&nbsp;</span>
                <span style="background-color:#ffeff5;border-radius: 5px;margin-left: 5px;" v-if="item.goodlb2!=''">&nbsp;&nbsp;{{item.goodlb2}}&nbsp;&nbsp;</span>
                <span style="background-color:#ffeff5;border-radius: 5px;margin-left: 5px;" v-else>&nbsp;&nbsp;{{item.goodlb1}}&nbsp;&nbsp;</span>
              </div>

              <div style="margin-top: 10px;width: 100%;height: 30px">
                <div style="color: #ff416b;font-size: 20px;width: 50%;float: left">¥{{item.goodprice}}</div>
                <div style="float: right;width: 50%">
                  <van-button icon="minus" type="primary" round style="height: 25px;width: 25px" @click="minus(index,indexx)"/>
                  <span style="margin: 0 2px;text-align: center;border-bottom: 1px solid black;">{{item.count}}</span>
                  <van-button icon="plus" type="primary" round style="height: 25px;width: 25px" @click="plus(index,indexx)"/>
                </div>
              </div>
            </div>
          </div>
          </div>

        </div>
      </div>

    </div>
  </div>

  <div style="height: 60px;position: fixed;bottom: 20px; width:90%;margin-left: 5%; background-color:#fff;border-radius: 15px;opacity: 0.8;padding: 0 5px;box-sizing: border-box" v-if="all_price>0">
    <van-button type="danger" round style="padding: 0 20px;float: right;height:46px;margin-top: 7px" @click="zhifu">支付({{all_count}})</van-button>
    <div style="float: right;color: red;margin-right: 10px;margin-top: 20px">
      <span style="font-size: 14px">合计：</span>
      <span style="font-size: 20px">¥ {{all_price}}</span>
    </div>

  </div>

  <div style="height: 100px;"></div>
</div>
</template>

<script>
import {Toast,Dialog} from "vant";


export default {
  name: "shopjiesuan",
  data(){
   return {
     user:{},
    selected_goods:[],
     all_price:0,
     count:0,
     xuanze:[
         {
      id:0,
       name:"手机数码",
       child:[]
     },{
       id:1,
       name:"电脑办公",
       child:[]
     },{
       id:2,
       name:"酒水饮料",
       child:[]
     },{
       id:3,
       name:"休闲零食",
       child:[]
     },{
       id:4,
       name:"新鲜水果",
       child:[]
     }],
     all_count:0,
     dingdan:{
       goodidlist:''
     }
   };
  },
  created() {
    this.init_user()
  },
  mounted() {
    this.check_user()
    this.init_selectedgood()
    this.reinit()
  },
  beforeDestroy() {
    this.toselected()
  },
  methods:{
    init_user(){
      this.user = this.$store.state.user
      // console.log(this.user)
      if (this.$store.state.this_selected_goods_list.length<1){
        this.$router.push('/shopclass')
      }
    },
    init_selectedgood(){
      this.selected_goods = []
      for (let i = 0; i < this.$store.state.this_selected_goods_list.length; i++) {
        this.all_price += this.$store.state.this_selected_goods_list[i].goodprice
        this.panduan(this.$store.state.this_selected_goods_list[i])
        this.all_count++
      }
      this.all_price = Math.floor(this.all_price*100)/100
      // console.log(this.selected_goods)

    },
    reinit(){
      for (let i = 0; i < this.selected_goods.length; i++) {
        if (this.selected_goods[i].goodclass === "手机数码"){
          this.xuanze[0].child.push(this.selected_goods[i])
        }else if (this.selected_goods[i].goodclass === "电脑办公"){
          this.xuanze[1].child.push(this.selected_goods[i])
        }else if (this.selected_goods[i].goodclass === "酒水饮料"){
          this.xuanze[2].child.push(this.selected_goods[i])
        }else if (this.selected_goods[i].goodclass === "休闲零食"){
          this.xuanze[3].child.push(this.selected_goods[i])
        }else if (this.selected_goods[i].goodclass === "新鲜水果"){
          this.xuanze[4].child.push(this.selected_goods[i])
        }
      }
    },
    check_user(){
      if(this.user.id === -1 || this.user.name === "" || this.user.surplus === -1 ){
        Toast.fail("请登录！")
        window.sessionStorage.clear()
        this.$router.push('/login')
        return false
      }
      return true
    },
    frqk(){
      Dialog.confirm({
        message: '确认清空购物车？',
      })
          .then(() => {
            // on confirm
            this.qk()
          })
          .catch(() => {
            // on cancel
          });
    },
    qk(){
      this.selected_goods = []
      this.xuanze = []
      this.all_price = 0
      this.$store.commit('set_selected_goods_list',[])
      this.all_count = 0
      this.$router.back()
    },
    onClickLeft(){
      this.$router.back()
      this.$store.commit('set_selected_goods_list',this.selected_goods)
    },
    zhifu(){
      Dialog.confirm({
        title:'确认支付',
        message: '确认支付'+this.all_price+'元?',
      })
          .then(() => {
            // on confirm
            this.confirmzhifu()
          })
          .catch(() => {
            // on cancel
            Toast.fail('用户取消')
          });
    },
    confirmzhifu(){
      this.toselected()
      if (this.user.surplus>=this.all_price*100){
        for (let i = 0; i < this.$store.state.this_selected_goods_list.length; i++) {
          if (this.$store.state.this_selected_goods_list[i].count>0){
            this.dingdan.goodidlist += this.$store.state.this_selected_goods_list[i].id+','
          }
        }
        this.dingdan.userid = this.user.id
        this.dingdan.username = this.user.name
        this.dingdan.createtime = Date.now()
        this.dingdan.dingdanid = Date.now() + '-' + this.user.id
        this.dingdan.allcount = this.all_price
        this.$http.post('/goods/dingdan',this.dingdan).then(res=>{

          // console.log(this.dingdan)
          this.$store.commit('set_buought_goods_id',this.dingdan.dingdanid)

          this.user.surplus -= this.all_price*100
          this.$http.post('/goods/dingdan/'+this.user.surplus+'/'+this.user.id).then(res=>{

          })
          Toast.success('支付成功！')
          this.toselected()
          // this.$store.commit('set_buought_goods_list',this.$store.state.this_selected_goods_list)
          this.selected_goods = []
          this.xuanze = []
          this.all_price = 0

          this.$store.commit('set_selected_goods_list',[])
          this.all_count = 0
          this.$router.push('/shopsuccess')
        }).catch((res) => {
          Toast.fail('服务器异常获取失败！');
        })

      }
      else {
        Toast.fail('余额不够！')
      }

    },
    panduan(arr){
      var flag = 0

      for (let i = 0; i < this.selected_goods.length; i++) {
        if (arr.id === this.selected_goods[i].id){
          this.selected_goods[i].count++
          flag = 1
          break
        }
      }

      if (flag === 0){
         this.selected_goods.push(arr)
      }
    },
    plus(index,indexx){
      this.xuanze[index].child[indexx].count = this.xuanze[index].child[indexx].count +1;
      this.all_price += this.xuanze[index].child[indexx].goodprice
      this.all_price = Math.floor(this.all_price*100)/100
      console.log(this.all_price)
      this.all_count++

      this.toselected()
    },
    minus(index,indexx){
      if (this.xuanze[index].child[indexx].count  === 1){
        Dialog.confirm({
          message: '确认删除此商品？',
        }).then(() => {
              // on confirm
              this.xuanze[index].child[indexx].count = this.xuanze[index].child[indexx].count - 1;
              this.all_price -= this.xuanze[index].child[indexx].goodprice
              this.all_price = Math.floor(this.all_price*100)/100
              this.all_count--

              if (this.xuanze[index].child[indexx].count <= 0){
                this.xuanze[index].child.splice(indexx,1)
              }
          if (this.selected_goods.length<2){
            this.qk()
          }
            })
            .catch(() => {
              // on cancel
            });
      }else {
        this.xuanze[index].child[indexx].count = this.xuanze[index].child[indexx].count - 1;
        this.all_price -= this.xuanze[index].child[indexx].goodprice
        this.all_price = Math.floor(this.all_price*100)/100
        this.all_count--
      }


      this.toselected()
    },
    toselected(){
      this.selected_goods = []
      this.$store.commit('set_selected_goods_list',[])
      for (let i = 0; i < this.xuanze.length; i++) {
        for (let j = 0; j < this.xuanze[i].child.length; j++) {
          for (let k = 0; k <this.xuanze[i].child[j].count; k++) {
            var cc = Object.assign({},this.xuanze[i].child[j])
            cc.count = 1
            this.selected_goods.push(cc)
          }
        }
      }
      // console.log(this.selected_goods)
      this.$store.commit('set_selected_goods_list',this.selected_goods)
    }
  }
}
</script>

<style scoped lang="less">

</style>
